<template>
    <div class="login">
       <center>
        <div class="login-form">
            <div class="title-form">логин</div>
            <input type="text" class="input-form" v-model='username'>
            <div class="title-form">пароль</div>
            <input type="text" class="input-form" v-model='password'>
            <div class="title-form">повторите пароль</div>
            <input type="text" class="input-form" v-model='passwordConfirm'>
            <div class="button-form" id="confirm-form" v-on:click='register()'>войти</div>
            <div class="button-form" id="confirm-form" v-on:click='login()'>регистрация</div>
            <div class="button-form" id="confirm-form" v-on:click='hide'>отмена</div>
        </div>
        </center>
    </div>
</template>
<script>
export default{
    name: 'login',
    data: function(){
        return {
            username: '',
            password: '',
            passwordConfirm: '',
            errorMessage: ''
        }
    },
    computed:   {
        
    },
    methods: {
        register(){
            //let username, password, errorMessage = this
            this.$store.dispatch(
                'REGISTER',
                {
                    'login': this.username,
                    'password': this.password,
                    'passwordConfirm': this.passwordConfirm
                }
            )
            this.$store.dispatch(
                'LOGIN',
                {
                    'login': this.username,
                    'password': this.password,
                    'passwordConfirm': this.passwordConfirm
                }
            )
        },
        login(){
            this.$store.dispatch(
                'LOGIN',
                {
                    'login': this.username,
                    'password': this.password,
                    'passwordConfirm': this.passwordConfirm
                }
            )
        },
        hide(){
            this.$store.dispatch('CHANGE_AUTORIZED_MENU')
        }
    }
}
</script>
