<template>
    <div class="traning_content">
        <h1>тренировка</h1>
        <center>
            <div class="back-button" v-on:click="back_button">назад</div>
        </center>
        <div class="prew">
            <div class="vid">
                <youtube
                    :video-id="vidId"
                    :player-vars="{
                                autoplay: 1 ,
                                mute: 1,  
                            }"
                ></youtube>
                
            </div>
        </div>
        
        
        <div class="traning_item" v-for="item in current_checklist">
            <div class="exercise">{{item.title}}</div>
            <div class="watch-button" v-on:click="set_vid_url(item.url)">смотреть ролик</div>
            <div v-for="rep in item.repetitions">
                <div class="content-check" v-on:click="checked({i:item.index, n:rep.index})" v-bind:class="{executed : rep.checked, complite : item.state}"></div>
            </div>
        </div>
        
        
    </div>
</template>
<script>

import { getIdFromURL, getTimeFromURL } from 'vue-youtube-embed'

export default {
    name: 'traning_content',
    data: function(){return{
        reps: '',
        count: 0,
        type: '',
        url: ''
    }},
    componets: {
        //TraningItem,
        //video_prew,
    },
    methods: {
        back_button: function(){
            this.$store.dispatch('CHANGE_MAINMENUSTATUS')
            this.$store.dispatch('SET_FLASH_CHECKLIST')
        },
        checked: function(i){
            this.$store.dispatch('CHECK_REPETITION', i)  
            this.$store.dispatch('GET_CHECHLIST_REPETITIONS_STATE', i.i)
        },
        set_vid_url: function(url){
            this.url = url
        }
        
    },
    computed: {
        current_checklist: function(){
            //this.reps = this.$store.getters.CHECKLIST
            return this.$store.getters.CHECKLIST
        },
        vidId: function(){
            return getIdFromURL(this.url)
        },
    },
    beforeCreate(){
        this.$store.dispatch('GET_CURRENT_CHECKLIST', {})
    }
    
}
    
</script>
